html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #FFFFFF;
  font-family: "Montserrat";
  color: #000000;
  align-items: center;
}

div {
  box-sizing: border-box;
}


h2 {
  font-size: 48px;
  font-family: "Montserrat";
  color: rgb(0, 0, 0);
  font-weight: bold;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: var(--second);
}

section {
  width: 100%;
}

button {
  color: var(--first);
  cursor: pointer;
  font-size: 24px;
  background-color: var(--second);
  padding: 5px 10px;
  border: 2px solid var(--second);
  font-family: Verdana, Geneva, Tahoma, sans-serif, Helvetica, sans-serif;
}

button:hover {
  background-color: var(--background);
}

button:focus,
input:focus {
  outline-width: 1px;
  outline-style: solid;
  outline-color: var(--first);
  outline-offset: 4px;
}

p {
  font-size: 18px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-bottom: 50px;
  text-align: center;
  line-height: 2;
}

ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  justify-content: space-around;
}

li {
  margin-bottom: 20px;
}