.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  color: #000000;
  width: 100%;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.square {
  width: 30%;
  position: relative;
}

.white-text {
  color: #ffffff;
}

.rect {
  width: 70%;
}
.dark {
  background-color: rgba(0, 0, 0, 0.65);
}

#tap-to-fly > h2{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#fly-higher > .square {
  padding-right: 100px;
}

img {
  width: 100%;
  height: auto;
}
